<template>
  <div class="dark2">
    <span class="bg2"></span>
    <v-card dark max-width="600" class="mx-auto">
      <v-toolbar extended class="mt-1" color="blue-grey" dark>
        <v-toolbar-title class="flex text-center">
          <h2 class="text-center mt-10">Quiz Dark 2</h2>
        </v-toolbar-title>
      </v-toolbar>

      <v-progress-linear color="lime lighten-1" :value="progress"></v-progress-linear>

      <v-list subheader two-line v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">
      <h1 class="text-center my-4">Domanda {{ b }}/{{ questions.length }}</h1>
      <v-list-item class="d-flex justify-center text-center" >{{ element.question }}</v-list-item>
      <v-divider class="mt-10"></v-divider>

      <v-list-item-group color="blue-grey--text">
        <template v-for="(item, index) in element.suggestions">
      <v-list-item id="list" class="d-flex justify-center my-2" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)" :disabled="lockSelection">
        {{ item.suggestion }}
        <v-icon class="mx-5" color="white" v-if="select ? item.correct : ''">mdi-check</v-icon>
        <v-icon class="mx-5" color="white" v-if="select ? !item.correct : ''">mdi-close</v-icon>
      </v-list-item>
      <v-divider v-if="index < element.suggestions.length - 1" :key="`${index}-divider`"></v-divider>
      </template>
      </v-list-item-group>
      </v-list>

      <v-divider></v-divider>

      <v-card dark max-width="600" class="mx-auto" v-if="score_show">
        <h2 class="text-center my-4">Il tuo punteggio </h2>
        <h2 class="text-center my-4">{{ score }}/{{ questions.length }}</h2>
        <v-card-actions class="justify-center">
          <v-btn class="mx-10" @click="restartQuiz">Ricomincia <v-icon right>mdi-reload</v-icon></v-btn>
          <v-btn class="mx-10" @click="$router.push('/')">Seleziona <v-icon right>mdi-home</v-icon></v-btn>
        </v-card-actions>
          <v-divider class="mt-5"></v-divider>
      </v-card>

      <v-card-actions class="justify-center">
        <v-btn class="mx-10" color="blue-grey darken-1" @click="skipQuestion">
          Salta
        </v-btn>
        <v-btn :disabled="!next" class="mx-10" color="blue-grey darken-1" @click="nextQuestion">
          Avanti
        </v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
export default {
  data(){
    return{
      questions: [
        {
          question:'Quante persone stanno lavorando nelle grotte per scavare i tunnel, il 21 giugno 1921?',
          suggestions: [
            {suggestion:'1'},
            {suggestion:'3'},
            {suggestion:'2', correct: true},
            {suggestion:'Nessuna'},
          ]
        },
        {
          question:"Come si chiama il detective che affiancherá la polizia di Winden per le indagini sulle persone scompare, fin dall'inizio della seconda stagione?",
          suggestions: [
            {suggestion:'Torben Wöller'},
            {suggestion:'Clausen', correct: true},
            {suggestion:'Jankowski'},
            {suggestion:'Nielsen'},
          ]
        },
        {
          question:"Chi è il leader dei sopravvissuti nella Winden del 2053?",
          suggestions: [
            {suggestion:'Claudia'},
            {suggestion:'Elisabeth', correct: true},
            {suggestion:'Silja'},
            {suggestion:'Agnes'},
          ]
        },
        {
          question:"Chi rivela a Claudia adulta dell'esistenza dei viaggi nel tempo, nel 1987?",
          suggestions: [
            {suggestion:'Noah'},
            {suggestion:'Claudia anziana', correct: true},
            {suggestion:'Jonas'},
            {suggestion:'Helge'},
          ]
        },
        {
          question:"In quale epoca Lo Straniero porta Hannah nel futuro grazie al dispositivo di Tannhaus?",
          suggestions: [
            {suggestion:'Nel 1954'},
            {suggestion:'Nel 1986'},
            {suggestion:'Nel 1987', correct: true},
            {suggestion:'Nel 1921'},
          ]
        },
        {
          question:"Come ottiene Claudia il dispositivo di Tannhaus per viaggiare nel tempo?",
          suggestions: [
            {suggestion:'Glielo consegna Lo Straniero'},
            {suggestion:'Glielo consegna Noah'},
            {suggestion:'Lo trova seppellito nel giardino di casa', correct: true},
            {suggestion:'Gielo consegna Claudia anziana'},
          ]
        },
        {
          question:"Come fa Helge bambino a tornare nel 1954?",
          suggestions: [
            {suggestion:'Con la sedia nel bunker', correct: true},
            {suggestion:'Viaggiando attraverso le grotte'},
            {suggestion:'Con il dispositivo di Tannhaus'},
            {suggestion:'Con la materia oscura'},
          ]
        },
        {
          question:"Chi rivela a Noah dove si trovano le pagine mancanti del libro Sic Mundis?",
          suggestions: [
            {suggestion:'Noah giovane'},
            {suggestion:'Claudia'},
            {suggestion:'Agnes', correct: true},
            {suggestion:'Adam'},
          ]
        },
        {
          question:"In quale epoca arriva Claudia Tiedemann nel suo primo viaggio nel tempo, grazie al dispositivo di Tannhaus?",
          suggestions: [
            {suggestion:'2019'},
            {suggestion:'2020', correct: true},
            {suggestion:'1954'},
            {suggestion:'1986'},
          ]
        },
        {
          question:"In quale epoca arriva Jonas dopo aver viaggiato attravero la materia oscura del 2053?",
          suggestions: [
            {suggestion:'1920'},
            {suggestion:'1921', correct: true},
            {suggestion:'1986'},
            {suggestion:'2020'},
          ]
        },
        {
          question:"Chi è presente con Jonas nella centrale nucleare quando entra nella materia oscura, nel 2053?",
          suggestions: [
            {suggestion:'Silja', correct: true},
            {suggestion:'Elisabeth adulta'},
            {suggestion:'Entrambe'},
            {suggestion:'Nessuno'},
          ]
        },
        {
          question:"Chi rivela a Katharina che Mikkel e Ulrich sono vivi ma in epoche diverse?",
          suggestions: [
            {suggestion:'Charlotte', correct: true},
            {suggestion:'Peter'},
            {suggestion:'Hannah'},
            {suggestion:'Lo Straniero'},
          ]
        },
        {
          question:"Chi incontra Jonas dopo essere stato condotto nella sede di Sic Mundus?" ,
          suggestions: [
            {suggestion:'Adam', correct: true},
            {suggestion:'Magnus'},
            {suggestion:'Agnes'},
            {suggestion:'Franziska'},
          ]
        },
        {
          question:"Perché Ulrich Nielsen fugge dall'ospedale psichiatrico nel quale è rinchiuso nel 1987?",
          suggestions: [
            {suggestion:'Per uccidere Egon'},
            {suggestion:'Per incontrare la madre'},
            {suggestion:'Per cercare Mikkel', correct: true},
            {suggestion:'Nessuna di queste'},
          ]
        },
         {
          question:"Dopo aver viaggiato nel tempo per la prima volta, in quale epoca si ritrovano Martha e gli altri ragazzi?",
          suggestions: [
            {suggestion:'1986'},
            {suggestion:'2019'},
            {suggestion:'1987', correct: true},
            {suggestion:'1954'},
          ]
        },
         {
          question:"Cosa scopre Charlotte parlando con Noah, all'interno del vecchio negozio di Tannhaus?",
          suggestions: [
            {suggestion:"L'identitá della madre"},
            {suggestion:"L'identitá del padre", correct: true},
            {suggestion:'Entrambe le cose'},
            {suggestion:'Nessuna di queste'},
          ]
        },
         {
          question:"In quale giorno e in che anno arriva Jonas dopo aver viaggiato con la materia oscura nella sede di Sic Mundus?",
          suggestions: [
            {suggestion:"20 Giugno 2020"},
            {suggestion:"21 Giugno 2019"},
            {suggestion:'20 Giugno 2019', correct: true},
            {suggestion:'Nessuna di queste'},
          ]
        },
        {
          question:"Cosa trovano Martha e Jonas nella sabbia quando si trovano al lago?",
          suggestions: [
            {suggestion:"Il medaglione di Sant'Anna"},
            {suggestion:"Il medaglione di San Cristoforo", correct: true},
            {suggestion:'Il medaglione di San Lorenzo'},
            {suggestion:'Il medaglione di San Claudio'},
          ]
        },
        {
          question:"Chi entra in casa Kahnwald mentre Jonas cerca di impedire al padre di impiccarsi?",
          suggestions: [
            {suggestion:"Adam"},
            {suggestion:"Noah"},
            {suggestion:'Claudia', correct: true},
            {suggestion:'Hannah'},
          ]
        },
        {
          question:"In quale epoca arriva Hannah nel suo secondo viaggio nel tempo?",
          suggestions: [
            {suggestion:"1954", correct: true},
            {suggestion:"1953"},
            {suggestion:'1986'},
            {suggestion:'1987'},
          ]
        },
        {
          question:"Per quale motivo il detective Clausen arresta Aleksander Tiedemann?",
          suggestions: [
            {suggestion:"Per l'omicidio del fratello"},
            {suggestion:"Per una rapina"},
            {suggestion:'Per frode bancaria'},
            {suggestion:"Per furto d'identitá", correct: true},
          ]
        },
        {
          question:"Chi entra in casa di Claudia Tiedemann nel 1987 per portarla nel 2020?",
          suggestions: [
            {suggestion:"Claudia anziana"},
            {suggestion:"Jonas", correct: true},
            {suggestion:'Helge'},
            {suggestion:"Agnes"},
          ]
        },
         {
          question:"Quante persone sono presenti nel bunker qualche istante prima che scoppi l'apocalisse?",
          suggestions: [
            {suggestion:"4"},
            {suggestion:"5", correct: true},
            {suggestion:'3'},
            {suggestion:"6"},
          ]
        },
        {
          question:"Quali personaggi sono stati salvati dallo Straniero poco prima dell'apocalisse?",
          suggestions: [
            {suggestion:"Magnus, Martha e Franziska"},
            {suggestion:"Elisabeth, Franziska e Bartosz"},
            {suggestion:'Martha, Magnus ed Elisabeth'},
            {suggestion:"Bartosz, Magnus e Franziska", correct: true},
          ]
        },
        {
          question:"Chi muore qualche istante prima che l'apocalisse colpisca Winden?",
          suggestions: [
            {suggestion:"Elisabeth adulta"},
            {suggestion:"Hannah"},
            {suggestion:'Martha', correct: true},
            {suggestion:"Charlotte"},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
      lockSelection: false,
    }
  },
  
  methods:{
    selectResponse(e){
      this.select = true;
      this.next = true;
      this.lockSelection = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){
      this.lockSelection = false;
      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },

    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  },
}
</script>

<style>
#list.correct{
  background-color: rgb(74, 219, 74);
}

#list.incorrect{
  background-color: rgb(240, 117, 100);
}

</style>