      </div>

      <v-progress-linear color="lime accent-2" :value="progress"></v-progress-linear>
      <div class="quiz-main" v-for="(element, index) in questions.slice(a,b)" :key="index" v-show="quiz">
        <div class="box-question">
          <h2>Domanda {{ b }}/{{ questions.length }}</h2>
          <p>{{ element.question }}</p>
        </div>
        <div class="box suggestions">
          <ul>
            <li v-for="(item, index) in element.suggestions" :key="index" :class="select ? check(item) :  ''" @click="selectResponse(item)">{{ item.suggestion }} <v-icon right color="white" v-if="select ? item.correct : ''">mdi-check</v-icon> <v-icon color="white" right v-if="select ? !item.correct : ''">mdi-close</v-icon></li>
          </ul>
        </div>
      </div>

      <div class="box-score" v-if="score_show">
        <h2>Il tuo punteggio </h2>
        <h2>{{ score }}/{{ questions.length }}</h2>
        <div class="btn-restart">
          <button @click="restartQuiz">Ricomincia <v-icon right>mdi-reload</v-icon></button>
          <button @click="$router.push('/')">Seleziona <v-icon right>mdi-home</v-icon></button>
        </div>
      </div>

      <div class="quiz-footer">
        <div class="box-button">
          <button @click="skipQuestion" :style="!next ? 'background-color: rgb(106, 128, 202)' : ''">Salta</button>
          <button @click="nextQuestion" :style="next ? 'background-color: rgb(106, 128, 202)' : ''">Avanti</button>
        </div>
      </div>
    </div>
  </div>
</template>

