            {suggestion:'1954'},
            {suggestion:'1986'},
          ]
        },
        {
          question:'Chi rivela a H.G. Tanhaus(l’orologiaio) l’esistenza del varco nelle grotte e dei viaggi nel tempo?',
          suggestions: [
            {suggestion:'Ulrich Nielsen'},
            {suggestion:'Claudia Tiedemann'},
            {suggestion:'Lo Straniero', correct: true},
            {suggestion:'Noah'},
          ]
        },
        {
          question:'Qual è il vero nome di Aleksander Tiedemann?',
          suggestions: [
            {suggestion:'Aleksander Köhler'},
            {suggestion:'Boris Köhler'},
            {suggestion:'Aleksander Niewald'},
            {suggestion:'Boris Niewald', correct: true},
          ]
        },
        {
          question:'Chi commissiona e quando ad H.G. Tanhaus(l’orologiaio) la costruzione della macchina del tempo portatile?',
          suggestions: [
