            {suggestion:'Noah nel 1953'},
            {suggestion:'Claudia Tidemann nel 1954', correct: true},
            {suggestion:'Lo Straniero nel 1986'},
          ]
        },
        {
          question:'Il corpo di quale ragazzo appare nel bunker, davanti a Peter Doppler, la sera del 4 novembre 2019?',
          suggestions: [
            {suggestion:'Mads', correct: true},
            {suggestion:'Yasin'},
            {suggestion:'Erik'},
            {suggestion:'Mikkel'},
          ]
        },
        {
          question:'Come muore Helge Doppler da anziano?',
          suggestions: [
            {suggestion:'Di vecchiaia'},
            {suggestion:'In un incidente stradale', correct: true},
            {suggestion:'Per via di un malore'},
            {suggestion:'Ucciso da Noah'},
          ]
        },
        {
          question:'In quali epoche finiscono Helge e Jonas dopo essersi toccati attraverso il wormhole?',
          suggestions: [
            {suggestion:'Helge 2019 - Jonas 1986'},
            {suggestion:'Helge 1953 - Jonas 2052'},
            {suggestion:'Helge 1986 - Jonas 2052', correct: true},
            {suggestion:'Helge 2052 - Jonas 2019'},
          ]
        },
      ],
      a: 0,
      b: 1,
      select: false,
      score: 0,
      quiz: true,
      score_show: false,
      next: false,
      progress: 0,
    }
  },
  methods: {
    selectResponse(e){
      this.select = true;
      this.next = true;

      if(e.correct) {
        this.score++;
      }
    },

    check(status){
      if (status.correct) {
        return 'correct'
      }
      else{
        return 'incorrect'
      }
    },

    nextQuestion(){

      if (!this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
        this.select = false;
        this.next = false;
      }
    },

    skipQuestion(){

      if (this.next) {
        return;
      }

      this.progress = this.progress + (100/this.questions.length);

      if (this.questions.length - 1 == this.a) {
        this.score_show = true;
        this.quiz = false;
      }
      else{
        this.a++;
        this.b++;
      }
    },
    restartQuiz(){
      Object.assign(this.$data, this.$options.data());
    }
  }
}
</script>

