import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Dark1 from '../views/Dark1.vue'
import Dark2 from '../views/Dark2.vue'
import Dark3 from '../views/Dark3.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/dark1',
    name: 'dark1',
    component: Dark1
  },
  {
    path: '/dark2',
    name: 'dark2',
    component: Dark2
  },
  {
    path: '/dark3',
    name: 'dark3',
    component: Dark3
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
